.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
  
    border-radius: 4px;
    padding: 32px 16px;
    background-color: #a6cedfbf;
  
    & input {
      padding: 8px;
      font-family: inherit;
      border: 1px solid rgba(33, 33, 33, 0.2);
      border-radius: 4px;
    }
  
    & label {
      color: gray;
      font-weight: 200;
      display: flex;
      flex-direction: column;
      margin-bottom: 12px;
    }
  }
  
  .label {
    display: flex;
    flex-direction: column;
    margin: 0.5rem auto;
    align-items: center;
    font-size: large;
    text-align: left;
    padding: 0.5rem 1rem 0.5rem 1rem;
  }
  .formBtn {
    margin-top: 1rem;
    margin-bottom: 2rem;
    background-color: rgba(23, 26, 32, 0.8);
    border: none;
    border-radius: 6px;
    color: #fff;
    flex-grow: 1;
    
    font-size: 15px;
    height: 36px;
    line-height: 20px;
    margin-left: 8px;
    margin-right: 6px;
    min-width: 185px;
    padding: 0 16px 0 16px;
    color: white;
    cursor: pointer;
  }
  
  .formBtn:hover {
    background-color: rgba(23, 26, 32, 1);
    cursor: pointer;
  }